.tip {
  position: absolute;
  top: 0.26667rem;
  left: 0;
  right: 0;
  z-index: -1;
  text-align: center;
  background-color: rgba(255, 0, 0, 0.5);
  height: 1.06667rem;
  line-height: 1.06667rem;
  color: #fff; }
  .tip.showAlert {
    animation: showAlert 2s; }

@keyframes showAlert {
  0 {
    transform: translateY(0); }
  20% {
    transform: translateY(100%); }
  50% {
    transform: translateY(100%); }
  80% {
    transform: translateY(100%); }
  100% {
    transform: translateY(0); } }

/**
 *
 * @authors FYC (fengyinchao.hust@gmail.com)
 * @date    2017-03-30 19:43
 */
.backToTop {
  position: fixed;
  right: 0.26667rem;
  bottom: 2.66667rem; }
  .backToTop i.back {
    font-size: 1.33333rem;
    color: #03A9F4; }

[data-dpr="1"] div {
  font-size: 16px; }
  [data-dpr="1"] div i {
    font-size: 20px; }
  [data-dpr="1"] div #title {
    font-weight: bold; }

[data-dpr="2"] div {
  font-size: 35px; }
  [data-dpr="2"] div i {
    font-size: 40px; }

[data-dpr="3"] div {
  font-size: 50px; }
  [data-dpr="3"] div i {
    font-size: 50px; }
  [data-dpr="3"] div .tl_bottom {
    font-style: bold; }

body {
  color: #fff;
  font-weight: 400;
  background-image: linear-gradient(to right, #e6e9f0 0%, #eef1f5 100%); }

.b_item {
  color: #ccc; }
  .b_item i {
    color: #ccc; }



