.login-detail {
  position: fixed;
  top: 1.33333rem;
  bottom: 0;
  left: 0;
  right: 0;
  color: black;
}
.login-detail .login1 {
    margin-top: 2.66667rem;
    height: 1.33333rem;
}
.login-detail .login1 input {
      border: none;
      border-bottom: 1px solid #03A9F4;
}
.login-detail .login1 input:focus {
        outline: none;
}
.login-detail .login1 input, .login-detail .login1 span {
      display: block;
      margin: 0 auto;
      width: 90%;
      text-align: center;
}
.login-detail .login1 span {
      margin-top: 0.66667rem;
      background-color: #03A9F4;
      color: #fff;
      letter-spacing: 0.4rem;
      height: 1.06667rem;
      line-height: 1.06667rem;
}



