.vfooter {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1.33333rem;
  background-color: #03A9F4;
  box-shadow: 0 0 10px 0 #03A9F4;
}
.vfooter ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.vfooter ul li {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 1.33333rem;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.vfooter ul li a {
        padding: 0.13333rem;
}
.vfooter ul li a i, .vfooter ul li a span {
          display: block;
          line-height: 0.53333rem;
          text-align: center;
}
.vfooter ul li .cnode-active {
        color: green;
}



