.vsidebar {
  /*font-size:16px !important;*/
  position: fixed;
  top: 0;
  left: -4rem;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  bottom: 0;
  width: 4rem;
  /*background-image: linear-gradient(to right, #0acffe 0%, #495aff 100%);*/
  background-color: #fff;
  z-index: 150;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-weight: 500;
}
.vsidebar .login {
    height: 4rem;
    min-height: 4rem;
    /*border-bottom:px2rem(1) solid #ccc;*/
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: #03A9F4;
}
.vsidebar .login div {
      /*background-color: red;*/
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      height: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      padding: 0.53333rem;
}
.vsidebar .login div img {
        height: 2.13333rem;
        border-radius: 50%;
        background-color: red;
        width: 2.13333rem;
}
.vsidebar .login div span {
        height: 0.8rem;
        line-height: 0.8rem;
        /*padding-bottom: 10px;*/
        width: 80%;
        color: yellow;
        text-align: center;
        /*text-decoration: underline;*/
}
.vsidebar .about {
    border-top: 0.01333rem solid #ccc;
    height: 1.33333rem;
    min-height: 1.33333rem;
    padding: 0.4rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: black;
    font-weight: bold;
    /*justify-content: center;*/
}
.vsidebar .about span i {
      padding: 0 0.4rem;
}
.vsidebar .topics {
    height: 4.66667rem;
    min-height: 4.66667rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    /*justify-content: center;*/
    padding: 0.4rem 0;
}
.vsidebar .topics ul {
      color: black;
      font-weight: bold;
      padding: 0.4rem;
}
.vsidebar .topics ul li {
        margin: 0.4rem 0;
}
.vsidebar .topics ul li i {
          padding: 0 0.4rem;
}
.show {
  -webkit-transform: translateX(4rem);
          transform: translateX(4rem);
  /*box-shadow: 0 0 10px 0 $main-tone;*/
}



