.comment_list {
  min-height: 2.66667rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  /*background-color: red;*/
  padding: 0.13333rem;
  border-top: 1px solid #ccc;
}
.comment_list .comment_top {
    height: 2rem;
    line-height: 2rem;
    /*background-color: green;*/
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.comment_list .comment_top .c_top_left {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding: 0 0 0 0.4rem;
}
.comment_list .comment_top .c_top_left img {
        width: 1.33333rem;
        height: 1.33333rem;
        border-radius: 50%;
        vertical-align: middle;
}
.comment_list .comment_top .c_top_left span {
        margin: 0 0.13333rem;
        color: blue;
}
.comment_list .comment_top .c_top_right {
      width: 2.66667rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: end;
          -ms-flex-pack: end;
              justify-content: flex-end;
      color: #03A9F4;
      /*float:right;*/
}
.comment_list .comment_top .c_top_right i {
        margin-left: 10px;
}
.comment_list .comment_bottom {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 0.4rem;
}



