.me-detail {
  position: fixed;
  top: 1.33333rem;
  bottom: 0;
  left: 0;
  right: 0;
  color: black;
  background-color: #EEEEEE;
}
.me-detail .me-header {
    height: 2.66667rem;
    position: relative;
    background-image: -webkit-linear-gradient(top, #4facfe 0%, #00f2fe 100%);
    background-image: linear-gradient(to bottom, #4facfe 0%, #00f2fe 100%);
}
.me-detail .me-header img {
      position: absolute;
      /*background-color: red;*/
      border: 0;
      top: 1.33333rem;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      width: 2.66667rem;
      height: 2.66667rem;
      background-image: -webkit-linear-gradient(top, #f5f7fa 0%, #c3cfe2 100%);
      background-image: linear-gradient(top, #f5f7fa 0%, #c3cfe2 100%);
      border-radius: 50%;
}
.me-detail .me-header .text {
      position: absolute;
      top: 4.26667rem;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      width: 5.33333rem;
      height: 2.66667rem;
      text-align: center;
      color: green;
}
.me-detail .me-item {
    background-color: white;
}
.me-detail .me-item span {
      height: 1.33333rem;
      display: block;
      line-height: 1.33333rem;
      padding-left: 0.66667rem;
      border-bottom: 1px solid #ccc;
}
.me-detail .me-item span i {
        margin-right: 0.66667rem;
        color: green;
}
.me-detail .me-item span i.right {
          float: right;
}
.me-detail .item1 {
    margin: 2.66667rem 0 1.33333rem 0;
}



