.detail {
  margin-top: 1.33333rem;
  color: black;
}
.detail .top1 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding-left: 0.26667rem;
    height: 1.33333rem;
    min-height: 1.33333rem;
    border-bottom: 0.01333rem solid #ccc;
    /*background-color: green;*/
}
.detail .top1 img {
      width: 0.66667rem;
      height: 0.66667rem;
      border-radius: 50%;
}
.detail .top1 span {
      margin-left: 0.26667rem;
      font-size: 14px;
      color: #83BD50;
}
.detail .top1 span.time {
        color: gray;
}
.detail .top2 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-bottom: 0.01333rem solid #ccc;
    min-height: 2.66667rem;
    padding: 0.4rem 0.4rem 0;
}
.detail .top2 .title {
      font-size: 24px;
      font-weight: 600;
      line-height: 1.3;
      -webkit-box-flex: 3;
          -ms-flex: 3;
              flex: 3;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.detail .top2 .a_detail {
      color: #83BD50;
      height: 1.33333rem;
      line-height: 1.33333rem;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.detail .top2 .a_detail i {
        margin-right: 0.66667rem;
}
.detail .content {
    padding: 0.4rem;
}
.detail .go_comment textarea {
    width: 90%;
    margin: 0 auto;
    height: 2.66667rem;
    display: block;
    border: 1px solid #03A9F4;
    box-shadow: 0 0 2px 0 #03A9F4;
}
.detail .go_comment .button_group {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.detail .go_comment .button_group button {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      border: none;
      height: 1.06667rem;
      background-color: #03A9F4;
      color: #fff;
      margin: 0.4rem;
}
.detail .unlogin {
    text-align: center;
    border-top: 0.01333rem solid #ccc;
    padding: 1.33333rem 0;
    letter-spacing: 0.05333rem;
}
.detail .unlogin span {
      color: #03A9F4;
}



